.article-hero {
  margin-bottom: 70px;

  .wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  .image {
    border-radius: clamp(10px, 2vw, 20px);
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      border-radius: clamp(10px, 2vw, 20px);
    }
  }

  .content {
    border-radius: clamp(10px, 2vw, 20px);
    background: #f8f8f8;
    padding: 30px;
    display: flex;
    flex-direction: column;

    h1 {
      margin: 0;
      font-size: clamp(22px, 3vw, 30px);
      margin-bottom: 12px;
    }
    p {
      margin: 0;
      margin-bottom: 20px;
      flex: 1 1 auto;

      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: var(--gray);
    }

    .bottom {
      display: flex;
      flex-wrap: wrap;
      gap: 15px;

      .bottom-item {
        border-radius: 100px;
        padding: 5px 9px;
        background: #eaf1ff;
        color: var(--accent);
        display: flex;
        align-items: center;
        gap: 10px;

        svg {
          fill: var(--accent);
        }
      }
    }
  }
}

// ----------------

.article-content {
  &__wrapper {
    display: grid;
    grid-template-columns: 200px 1fr;
    gap: 40px;
  }

  &__nav {
    &-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      margin-bottom: 16px;
    }
    .list {
      display: flex;
      flex-direction: column;
      gap: 10px;

      a {
        border-radius: 10px;
        padding: 10px 23px 10px 13px;
        background: var(--blue);
        transition: var(--transition);
        color: var(--accent);
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 12px;
        line-height: 97%;
        letter-spacing: -0.01em;
        display: block;
        @media (any-hover: hover) {
          &:hover {
            background: var(--hovered);
            color: var(--white);
          }
        }
      }
    }
  }

  .content {
    display: flex;
    flex-direction: column;

    h2 {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(22px, 3vw, 30px);
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      margin: 0;
      margin-bottom: 30px;
    }

    p {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: var(--gray);
    }

    .content-block {
      border-radius: clamp(10px, 2vw, 20px);
      padding: 23px 30px;
      background: #f8f8f8;
      margin-bottom: clamp(25px, 3vw, 50px);
      @media (max-width: 992px) {
        padding: 20px 10px;
      }

      ul {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 20px;
      }
    }

    .content-block-yellow {
      background: #fdffdd;
    }

    .content-block-blue {
      background: var(--blue);
    }
    .offer__items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 22px;
      margin-bottom: clamp(25px, 3vw, 50px);
    }

    .content-info {
      border-radius: 20px;
      background: var(--accent);
      padding: 32px 40px 0 40px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 20px;
      margin-bottom: clamp(25px, 3vw, 50px);
      .info-left {
        display: flex;
        flex-direction: column;
        padding-bottom: 32px;
        .top {
          display: flex;
          align-items: center;
          gap: 10px;
          font-family: var(--font-family);
          font-weight: 500;
          font-size: 16px;
          line-height: 97%;
          letter-spacing: -0.01em;
          color: #fff;
          margin-bottom: 35px;
          &:before {
            content: "";
            width: 34px;
            height: 34px;
            background: url("../../img/icons/stars.svg") center / cover
              no-repeat;
          }
        }
      }

      .info-right {
        display: flex;
        justify-content: center;
      }

      .info-title {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: clamp(22px, 4vw, 35px);
        line-height: 97%;
        letter-spacing: -0.01em;
        color: #fff;
        flex: 1 1 auto;
        margin-bottom: 20px;
        span {
          opacity: 0.5;
        }
      }

      .info-btn {
        background: var(--white);
        color: var(--gray);
        padding-block: 17px;
        max-width: 305px;
        width: 100%;

        @media (any-hover: hover) {
          &:hover {
            background: var(--hovered);
            color: var(--white);
          }
        }
      }
    }

    // author
    .author {
      display: grid;
      grid-template-columns: 1fr 0.5fr;
      gap: 20px;
      margin-bottom: clamp(25px, 3vw, 50px);
      &-person {
        border-radius: 10px;
        background: var(--accent);
        padding: 27px;
      }
      &-socials {
        border-radius: 10px;
        background: var(--accent);
        padding: 27px;
        display: flex;
        flex-direction: column;
      }

      .author-person-top {
        display: flex;
        align-items: center;
        gap: 7px;
        background: var(--white);
        max-width: fit-content;
        padding: 10px 22px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--accent);
        margin-bottom: 24px;
        border-radius: 100px;
        svg {
          fill: var(--accent);
        }
      }
      .author-person-bottom {
        border-radius: 20px;
        background: var(--white);
        display: grid;
        grid-template-columns: 80px 1fr;
        gap: 24px;
        align-items: center;
        padding: 12px 17px;

        .name {
          font-family: var(--font-family);
          font-weight: 700;
          font-size: 22px;
          line-height: 110%;
          letter-spacing: -0.02em;
          color: var(--gray);
          display: flex;
          flex-direction: column;
          gap: 14px;

          span {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 16px;
            line-height: 97%;
            letter-spacing: -0.01em;
            color: var(--gray);
          }
        }
      }

      .soc {
        display: flex;
        gap: 16px;
        margin-top: auto;
        a {
          width: 60px;
          height: 60px;
          display: grid;
          place-items: center;
          background: var(--white);
          border-radius: 100px;
          transition: var(--transition);
          .icon {
            width: 27px;
            height: 27px;
            fill: var(--accent);
          }

          @media (any-hover: hover) {
            &:hover {
              background: var(--hovered);

              svg {
                fill: var(--white);
              }
            }
          }
        }
      }
    }

    .through__items {
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;
      margin-bottom: 100px;
    }
  }

  .comments {
    display: flex;
    flex-direction: column;
    margin-bottom: 100px;
    .comments-block {
      display: flex;
      flex-direction: column;
      gap: 18px;
      margin-bottom: 28px;
      .input {
        background: #eaf1ff;
        height: 158px;
        border-radius: 20px;
        font-family: var(--second-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 94%;
        letter-spacing: -0.02em;
        color: var(--accent);
        padding: 27px 25px;
        &::placeholder {
          color: inherit;
        }
      }
    }

    .comment-btn {
      background: #c5d1ff;
      color: var(--black);
      padding-block: 22px;
      border-radius: 10px;

      @media (any-hover: hover) {
        &:hover {
          background: var(--accent);
          color: var(--white);
        }
      }
    }
  }

  .comments-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  .comments-list-subtitle {
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 94%;
    letter-spacing: -0.02em;
    color: var(--gray);
    opacity: 0.7;
  }

  .comments-item {
    display: grid;
    grid-template-columns: 67px 1fr;
    gap: 15px;
    background: #f9f9f9;
    border-radius: 20px;
    padding: 20px 25px;

    .comments-item-content {
      display: flex;
      flex-direction: column;

      .comment-item-name {
        font-family: var(--second-family);
        font-weight: 700;
        font-size: clamp(16px, 2vw, 22px);
        line-height: 110%;
        letter-spacing: -0.02em;
        color: var(--accent);
        margin-bottom: 15px;
      }
      .comment-item-date {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: #949494;
        margin-bottom: 20px;
      }
      .comment-item-desc {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
        margin-bottom: 21px;
      }
      .comment-item-action {
        background: var(--white);
        padding: 12px;
        border-radius: 10px;
        display: flex;
        align-items: center;
        gap: 10px;
        max-width: fit-content;
      }

      .comment-item-action-btn {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 12px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);

        @media (any-hover: hover) {
          &:hover {
            svg {
              fill: var(--accent);
            }
            color: var(--accent);
          }
        }
      }
    }
  }
}
